export default {
  name: 'HypertextIndex',
  props: {
    chapters: {
      type: Array, // The array should contain a series of elements with the attribute title -> it will be used as href and displayed
      required: true,
    },
  },
  methods: {
    dash(index) {
      if (index < this.chapters.length - 1) {
        return '  -  '
      }
      return ''
    },
  },
}
